header {
  grid-area: header;
  display: flex;
  justify-content: space-between;
  font-family: $primary;
  background: linear-gradient($grey, #2B544B, $background-green);
}

h1 {
  font-size: 55px;
  color: $text-green; 
  margin: 10px 20px;
}

.fa-question-circle {
  font-size: 40px;
  margin: 10px 20px;
  color: $text-green;
  position: absolute;
  top: 10px;
  right: 10px;
  &:hover {
    transform: scale(1.5);
    transition-duration: .5s;
    cursor: pointer;
  }
}