@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,700);
body {
  background-color: #9BCCC3; }

* {
  box-sizing: border-box; }

.App {
  display: grid;
  grid-template-columns: 1.3fr 4fr;
  grid-template-rows: 110px 1fr;
  grid-template-areas: 'header header' 'aside question'; }

.button-div {
  box-shadow: 4px 4px 4px #35393C;
  background-color: #397367;
  border-radius: 5px;
  height: 500px;
  width: 700px;
  grid-area: question;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.left-off-button,
.wrong-button {
  background-color: #35393C;
  border: none;
  border-radius: 5px;
  color: #DCE8EA;
  font-family: "Montserrat", sans-serif;
  font-size: 30px;
  font-weight: 500;
  height: 70px;
  width: 450px;
  margin: 20px; }
  .left-off-button:hover,
  .wrong-button:hover {
    box-shadow: inset 0px 0px 0px 3px #DCE8EA;
    cursor: pointer;
    -webkit-transition-duration: .5s;
            transition-duration: .5s; }

.start-button {
  background-color: #35393C;
  border: none;
  border-radius: 5px;
  color: #DCE8EA;
  font-family: "Montserrat", sans-serif;
  font-size: 40px;
  font-weight: 500;
  height: 70px;
  width: 345px;
  margin: 20px; }
  .start-button:hover {
    box-shadow: inset 0px 0px 0px 3px #DCE8EA;
    cursor: pointer;
    -webkit-transition-duration: .5s;
            transition-duration: .5s; }

aside {
  grid-area: aside; }

.score-div {
  box-shadow: 4px 4px 4px #35393C;
  background-color: #35393C;
  border-radius: 5px;
  height: 200px;
